import React, { useState } from 'react';
import clsx from 'clsx';
import { Link, Unlink, Image, Palette } from 'lucide-react';
import ColorPicker from './ColorPicker';
import MediaLibraryButton from './MediaLibraryButton';

function SpacingCompass({ 
  label,
  margin = { top: 0, right: 0, bottom: 0, left: 0 },
  border = { top: 0, right: 0, bottom: 0, left: 0 },
  borderStyle = 'solid',
  borderColor = '#000000',
  padding = { top: 0, right: 0, bottom: 0, left: 0 },
  backgroundColor = '#ffffff',
  backgroundImage = '',
  backgroundSize = 'cover',
  backgroundPosition = 'center',
  backgroundRepeat = 'no-repeat',
  onMarginChange,
  onBorderChange,
  onBorderStyleChange,
  onBorderColorChange,
  onPaddingChange,
  onBackgroundColorChange,
  onBackgroundImageChange,
  onBackgroundSizeChange,
  onBackgroundPositionChange,
  onBackgroundRepeatChange,
  unit = 'px',
  min = 0,
  max = 100,
  className
}) {
  const [isMarginLinked, setIsMarginLinked] = useState(
    margin.top === margin.right && 
    margin.top === margin.bottom && 
    margin.top === margin.left
  );
  
  const [isBorderLinked, setIsBorderLinked] = useState(
    border.top === border.right && 
    border.top === border.bottom && 
    border.top === border.left
  );
  
  const [isPaddingLinked, setIsPaddingLinked] = useState(
    padding.top === padding.right && 
    padding.top === padding.bottom && 
    padding.top === padding.left
  );

  const [showColorPicker, setShowColorPicker] = useState(false);
  const [showImageInput, setShowImageInput] = useState(false);
  const [showBorderColor, setShowBorderColor] = useState(false);

  const handleMarginChange = (side, value) => {
    const numValue = parseInt(value) || 0;
    
    if (isMarginLinked) {
      onMarginChange({
        top: numValue,
        right: numValue,
        bottom: numValue,
        left: numValue
      });
    } else {
      onMarginChange({
        ...margin,
        [side]: numValue
      });
    }
  };

  const handleBorderChange = (side, value) => {
    const numValue = parseInt(value) || 0;
    
    if (isBorderLinked) {
      onBorderChange({
        top: numValue,
        right: numValue,
        bottom: numValue,
        left: numValue
      });
    } else {
      onBorderChange({
        ...border,
        [side]: numValue
      });
    }
  };

  const handlePaddingChange = (side, value) => {
    const numValue = parseInt(value) || 0;
    
    if (isPaddingLinked) {
      onPaddingChange({
        top: numValue,
        right: numValue,
        bottom: numValue,
        left: numValue
      });
    } else {
      onPaddingChange({
        ...padding,
        [side]: numValue
      });
    }
  };

  const renderInput = (type, side, value, position) => {
    const isMargin = type === 'margin';
    const isBorder = type === 'border';
    const isPadding = type === 'padding';
    
    let handleChange, isLinked;
    if (isMargin) {
      handleChange = handleMarginChange;
      isLinked = isMarginLinked;
    } else if (isBorder) {
      handleChange = handleBorderChange;
      isLinked = isBorderLinked;
    } else {
      handleChange = handlePaddingChange;
      isLinked = isPaddingLinked;
    }
    
    // Hide non-top inputs when linked
    if (isLinked && side !== 'top') {
      return null;
    }
    
    return (
      <div 
        className={clsx(
          'ev2-absolute ev2-flex ev2-items-center ev2-justify-center ev2-z-10',
          position
        )}
      >
        <div className="ev2-relative ev2-group">
          <input
            type="number"
            min={min}
            max={max}
            value={value}
            onChange={(e) => {
              e.stopPropagation();
              handleChange(side, e.target.value);
            }}
            onFocus={(e) => e.stopPropagation()}
            onBlur={(e) => e.stopPropagation()}
            onClick={(e) => e.stopPropagation()}
            data-no-super-forms="true"
            className={clsx(
              'ev2-w-12 ev2-h-8 ev2-text-sm ev2-text-center ev2-border-2 ev2-rounded-md',
              'focus:ev2-ring-2 focus:ev2-border-transparent ev2-transition-all',
              'ev2-bg-white ev2-shadow-sm hover:ev2-shadow-md',
              isMargin 
                ? 'ev2-border-orange-400 focus:ev2-ring-orange-500 hover:ev2-border-orange-500' 
                : isBorder
                ? 'ev2-border-green-400 focus:ev2-ring-green-500 hover:ev2-border-green-500'
                : 'ev2-border-blue-400 focus:ev2-ring-blue-500 hover:ev2-border-blue-500'
            )}
            title={`${type} ${isLinked ? '(all sides)' : side}`}
          />
        </div>
      </div>
    );
  };

  const borderStyles = [
    { value: 'none', label: 'None' },
    { value: 'solid', label: 'Solid' },
    { value: 'dashed', label: 'Dashed' },
    { value: 'dotted', label: 'Dotted' },
  ];

  return (
    <div className={clsx('ev2-space-y-4 ev2-max-w-xl', className)}>
      {label && (
        <label className="ev2-text-sm ev2-font-medium ev2-text-gray-700">
          {label}
        </label>
      )}
      
      <div className="ev2-relative" style={{ width: '480px', height: '480px', marginLeft: '0', marginRight: '80px' }}>
        {/* Legend positioned outside on the right */}
        <div className="ev2-absolute ev2-top-0 ev2-left-full ev2-ml-4 ev2-flex ev2-flex-col ev2-gap-1 ev2-text-xs ev2-bg-white ev2-p-2 ev2-rounded ev2-shadow-sm ev2-border ev2-border-gray-200">
          <div className="ev2-flex ev2-items-center ev2-gap-1">
            <div className="ev2-w-3 ev2-h-3 ev2-bg-orange-400 ev2-rounded-sm"></div>
            <span className="ev2-text-gray-600">Margin</span>
          </div>
          <div className="ev2-flex ev2-items-center ev2-gap-1">
            <div className="ev2-w-3 ev2-h-3 ev2-bg-green-400 ev2-rounded-sm"></div>
            <span className="ev2-text-gray-600">Border</span>
          </div>
          <div className="ev2-flex ev2-items-center ev2-gap-1">
            <div className="ev2-w-3 ev2-h-3 ev2-bg-blue-400 ev2-rounded-sm"></div>
            <span className="ev2-text-gray-600">Padding</span>
          </div>
        </div>

        {/* Background SVG */}
        <svg 
          viewBox="0 0 480 480" 
          className="ev2-w-full ev2-h-full ev2-absolute ev2-inset-0"
        >
          {/* Outer margin area */}
          <rect
            x="20"
            y="20"
            width="440"
            height="440"
            rx="8"
            fill="none"
            stroke="#fed7aa"
            strokeWidth="2"
            strokeDasharray="6 3"
          />
          
          {/* Margin fill areas */}
          <path
            d="M 20 28 L 240 240 L 460 28 L 460 20 Q 460 20 452 20 L 28 20 Q 20 20 20 28"
            fill="#fed7aa"
            opacity="0.2"
          />
          <path
            d="M 452 20 L 240 240 L 452 460 L 460 460 Q 460 460 460 452 L 460 28 Q 460 20 452 20"
            fill="#fed7aa"
            opacity="0.2"
          />
          <path
            d="M 460 452 L 240 240 L 20 452 L 20 460 Q 20 460 28 460 L 452 460 Q 460 460 460 452"
            fill="#fed7aa"
            opacity="0.2"
          />
          <path
            d="M 28 460 L 240 240 L 28 20 L 20 20 Q 20 20 20 28 L 20 452 Q 20 460 28 460"
            fill="#fed7aa"
            opacity="0.2"
          />
          
          {/* Border area */}
          <rect
            x="70"
            y="70"
            width="340"
            height="340"
            rx="6"
            fill="none"
            stroke="#86efac"
            strokeWidth="2"
            strokeDasharray="6 3"
          />
          
          {/* Border fill areas */}
          <path
            d="M 70 76 L 240 240 L 410 76 L 410 70 Q 410 70 404 70 L 76 70 Q 70 70 70 76"
            fill="#86efac"
            opacity="0.2"
          />
          <path
            d="M 404 70 L 240 240 L 404 410 L 410 410 Q 410 410 410 404 L 410 76 Q 410 70 404 70"
            fill="#86efac"
            opacity="0.2"
          />
          <path
            d="M 410 404 L 240 240 L 70 404 L 70 410 Q 70 410 76 410 L 404 410 Q 410 410 410 404"
            fill="#86efac"
            opacity="0.2"
          />
          <path
            d="M 76 410 L 240 240 L 76 70 L 70 70 Q 70 70 70 76 L 70 404 Q 70 410 76 410"
            fill="#86efac"
            opacity="0.2"
          />
          
          {/* Inner padding area */}
          <rect
            x="120"
            y="120"
            width="240"
            height="240"
            rx="4"
            fill="none"
            stroke="#bfdbfe"
            strokeWidth="2"
            strokeDasharray="6 3"
          />
          
          {/* Padding fill areas */}
          <path
            d="M 120 124 L 240 240 L 360 124 L 360 120 Q 360 120 356 120 L 124 120 Q 120 120 120 124"
            fill="#bfdbfe"
            opacity="0.2"
          />
          <path
            d="M 356 120 L 240 240 L 356 360 L 360 360 Q 360 360 360 356 L 360 124 Q 360 120 356 120"
            fill="#bfdbfe"
            opacity="0.2"
          />
          <path
            d="M 360 356 L 240 240 L 120 356 L 120 360 Q 120 360 124 360 L 356 360 Q 360 360 360 356"
            fill="#bfdbfe"
            opacity="0.2"
          />
          <path
            d="M 124 360 L 240 240 L 124 120 L 120 120 Q 120 120 120 124 L 120 356 Q 120 360 124 360"
            fill="#bfdbfe"
            opacity="0.2"
          />
          
          {/* Content area */}
          <rect
            x="170"
            y="170"
            width="140"
            height="140"
            rx="2"
            fill={backgroundColor || '#ffffff'}
            stroke="#e5e7eb"
            strokeWidth="1"
          />
          
          {/* Background image preview if set */}
          {backgroundImage && (
            <clipPath id="contentClip">
              <rect x="170" y="170" width="140" height="140" rx="2" />
            </clipPath>
          )}
        </svg>

        {/* Background image overlay */}
        {backgroundImage && (
          <div 
            className="ev2-absolute ev2-rounded"
            style={{
              left: '170px',
              top: '170px',
              width: '140px',
              height: '140px',
              backgroundImage: `url(${backgroundImage})`,
              backgroundSize: 'cover',
              backgroundPosition: 'center',
              opacity: 0.8
            }}
          />
        )}

        {/* Margin inputs - positioned at compass points */}
        {renderInput('margin', 'top', margin.top, 'ev2-top-0 ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('margin', 'right', margin.right, 'ev2-top-1/2 ev2-right-0 -ev2-translate-y-1/2')}
        {renderInput('margin', 'bottom', margin.bottom, 'ev2-bottom-0 ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('margin', 'left', margin.left, 'ev2-top-1/2 ev2-left-0 -ev2-translate-y-1/2')}

        {/* Border inputs - positioned at middle ring */}
        {renderInput('border', 'top', border.top, 'ev2-top-[58px] ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('border', 'right', border.right, 'ev2-top-1/2 ev2-right-[58px] -ev2-translate-y-1/2')}
        {renderInput('border', 'bottom', border.bottom, 'ev2-bottom-[58px] ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('border', 'left', border.left, 'ev2-top-1/2 ev2-left-[58px] -ev2-translate-y-1/2')}

        {/* Padding inputs - positioned at inner compass points */}
        {renderInput('padding', 'top', padding.top, 'ev2-top-[108px] ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('padding', 'right', padding.right, 'ev2-top-1/2 ev2-right-[108px] -ev2-translate-y-1/2')}
        {renderInput('padding', 'bottom', padding.bottom, 'ev2-bottom-[108px] ev2-left-1/2 -ev2-translate-x-1/2')}
        {renderInput('padding', 'left', padding.left, 'ev2-top-1/2 ev2-left-[108px] -ev2-translate-y-1/2')}

        {/* Center controls */}
        <div className="ev2-absolute ev2-top-1/2 ev2-left-1/2 -ev2-translate-x-1/2 -ev2-translate-y-1/2 ev2-z-20">
          <div className="ev2-flex ev2-flex-col ev2-items-center ev2-gap-2">
            {/* Link/Unlink buttons */}
            <div className="ev2-flex ev2-gap-1">
              <button
                type="button"
                onClick={() => setIsMarginLinked(!isMarginLinked)}
                className={clsx(
                  'ev2-p-1.5 ev2-rounded ev2-transition-all ev2-shadow-sm',
                  isMarginLinked 
                    ? 'ev2-bg-orange-500 ev2-text-white hover:ev2-bg-orange-600' 
                    : 'ev2-bg-white ev2-text-orange-600 ev2-border ev2-border-orange-300 hover:ev2-bg-orange-50'
                )}
                title={isMarginLinked ? 'Unlink margins' : 'Link margins'}
              >
                {isMarginLinked ? <Link className="ev2-w-3 ev2-h-3" /> : <Unlink className="ev2-w-3 ev2-h-3" />}
              </button>
              
              <button
                type="button"
                onClick={() => setIsBorderLinked(!isBorderLinked)}
                className={clsx(
                  'ev2-p-1.5 ev2-rounded ev2-transition-all ev2-shadow-sm',
                  isBorderLinked 
                    ? 'ev2-bg-green-500 ev2-text-white hover:ev2-bg-green-600' 
                    : 'ev2-bg-white ev2-text-green-600 ev2-border ev2-border-green-300 hover:ev2-bg-green-50'
                )}
                title={isBorderLinked ? 'Unlink borders' : 'Link borders'}
              >
                {isBorderLinked ? <Link className="ev2-w-3 ev2-h-3" /> : <Unlink className="ev2-w-3 ev2-h-3" />}
              </button>
              
              <button
                type="button"
                onClick={() => setIsPaddingLinked(!isPaddingLinked)}
                className={clsx(
                  'ev2-p-1.5 ev2-rounded ev2-transition-all ev2-shadow-sm',
                  isPaddingLinked 
                    ? 'ev2-bg-blue-500 ev2-text-white hover:ev2-bg-blue-600' 
                    : 'ev2-bg-white ev2-text-blue-600 ev2-border ev2-border-blue-300 hover:ev2-bg-blue-50'
                )}
                title={isPaddingLinked ? 'Unlink padding' : 'Link padding'}
              >
                {isPaddingLinked ? <Link className="ev2-w-3 ev2-h-3" /> : <Unlink className="ev2-w-3 ev2-h-3" />}
              </button>
            </div>

            {/* Background controls */}
            <div className="ev2-flex ev2-gap-1">
              <button
                type="button"
                onClick={() => {
                  setShowColorPicker(!showColorPicker);
                  setShowImageInput(false);
                  setShowBorderColor(false);
                }}
                className={clsx(
                  'ev2-p-1.5 ev2-rounded ev2-transition-all ev2-shadow-sm',
                  showColorPicker
                    ? 'ev2-bg-purple-500 ev2-text-white'
                    : 'ev2-bg-white ev2-border ev2-border-gray-300 hover:ev2-bg-gray-50'
                )}
                title="Background color"
              >
                <Palette className="ev2-w-3 ev2-h-3" />
              </button>
              
              <button
                type="button"
                onClick={() => {
                  setShowImageInput(!showImageInput);
                  setShowColorPicker(false);
                  setShowBorderColor(false);
                }}
                className={clsx(
                  'ev2-p-1.5 ev2-rounded ev2-transition-all ev2-shadow-sm',
                  showImageInput
                    ? 'ev2-bg-purple-500 ev2-text-white'
                    : 'ev2-bg-white ev2-border ev2-border-gray-300 hover:ev2-bg-gray-50'
                )}
                title="Background image"
              >
                <Image className="ev2-w-3 ev2-h-3" />
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* Border style and color controls */}
      <div className="ev2-flex ev2-gap-3 ev2-items-start">
        <div className="ev2-flex-1">
          <label className="ev2-text-xs ev2-font-medium ev2-text-gray-600 ev2-mb-1 ev2-block">
            Border Style
          </label>
          <select
            value={borderStyle}
            onChange={(e) => {
              onBorderStyleChange(e.target.value);
              if (e.target.value === 'none') {
                setShowBorderColor(false);
              }
            }}
            className="ev2-w-full ev2-px-3 ev2-py-1.5 ev2-text-sm ev2-border ev2-border-gray-300 ev2-rounded-md focus:ev2-ring-2 focus:ev2-ring-green-500 focus:ev2-border-transparent ev2-bg-white"
          >
            {borderStyles.map((style) => (
              <option key={style.value} value={style.value}>
                {style.label}
              </option>
            ))}
          </select>
        </div>
        
        {borderStyle !== 'none' && (
          <div className="ev2-flex-1">
            <label className="ev2-text-xs ev2-font-medium ev2-text-gray-600 ev2-mb-1 ev2-block">
              Border Color
            </label>
            <button
              type="button"
              onClick={() => {
                setShowBorderColor(!showBorderColor);
                setShowColorPicker(false);
                setShowImageInput(false);
              }}
              className="ev2-w-full ev2-flex ev2-items-center ev2-gap-2 ev2-px-3 ev2-py-1.5 ev2-bg-white ev2-border ev2-border-gray-300 ev2-rounded-md hover:ev2-border-gray-400 ev2-transition-colors"
            >
              <div 
                className="ev2-w-4 ev2-h-4 ev2-rounded ev2-border ev2-border-gray-300"
                style={{ backgroundColor: borderColor }}
              />
              <span className="ev2-text-sm ev2-font-mono">{borderColor}</span>
            </button>
          </div>
        )}
      </div>

      {/* Background controls panels */}
      {showColorPicker && (
        <div className="ev2-mt-4 ev2-p-4 ev2-bg-gray-50 ev2-rounded-lg ev2-border ev2-border-gray-200">
          <ColorPicker
            label="Background Color"
            value={backgroundColor}
            onChange={onBackgroundColorChange}
          />
        </div>
      )}

      {showBorderColor && (
        <div className="ev2-mt-4 ev2-p-4 ev2-bg-gray-50 ev2-rounded-lg ev2-border ev2-border-gray-200">
          <ColorPicker
            label="Border Color"
            value={borderColor}
            onChange={onBorderColorChange}
          />
        </div>
      )}

      {showImageInput && (
        <div className="ev2-mt-4 ev2-p-4 ev2-bg-gray-50 ev2-rounded-lg ev2-border ev2-border-gray-200">
          <div className="ev2-space-y-3">
            <label className="ev2-text-sm ev2-font-medium ev2-text-gray-700">
              Background Image
            </label>
            
            <div className="ev2-flex ev2-items-center ev2-gap-2">
              <MediaLibraryButton
                value={backgroundImage}
                onChange={onBackgroundImageChange}
                buttonText="Choose from Media Library"
                className="ev2-flex-shrink-0"
              />
              <span className="ev2-text-sm ev2-text-gray-500">or</span>
            </div>
            
            <input
              type="text"
              value={backgroundImage}
              onChange={(e) => onBackgroundImageChange(e.target.value)}
              placeholder="Enter image URL directly"
              className="ev2-w-full ev2-px-3 ev2-py-2 ev2-border ev2-border-gray-300 ev2-rounded-md focus:ev2-ring-2 focus:ev2-ring-primary-500 focus:ev2-border-transparent ev2-text-sm"
            />
            
            {backgroundImage && (
              <>
                <div className="ev2-flex ev2-items-start ev2-gap-3">
                  <img 
                    src={backgroundImage} 
                    alt="Background preview" 
                    className="ev2-w-20 ev2-h-20 ev2-object-cover ev2-rounded ev2-border ev2-border-gray-300"
                    onError={(e) => {
                      e.target.style.display = 'none';
                    }}
                  />
                  <div className="ev2-flex-1">
                    <p className="ev2-text-xs ev2-text-gray-600 ev2-break-all ev2-mb-2">{backgroundImage}</p>
                    <button
                      type="button"
                      onClick={() => onBackgroundImageChange('')}
                      className="ev2-text-sm ev2-text-red-600 hover:ev2-text-red-700 ev2-font-medium"
                    >
                      Remove image
                    </button>
                  </div>
                </div>
                
                <div className="ev2-grid ev2-grid-cols-3 ev2-gap-3">
                  <div>
                    <label className="ev2-text-xs ev2-font-medium ev2-text-gray-600 ev2-mb-1 ev2-block">
                      Size
                    </label>
                    <select
                      value={backgroundSize}
                      onChange={(e) => onBackgroundSizeChange(e.target.value)}
                      className="ev2-w-full ev2-px-2 ev2-py-1 ev2-text-sm ev2-border ev2-border-gray-300 ev2-rounded-md focus:ev2-ring-2 focus:ev2-ring-purple-500 focus:ev2-border-transparent ev2-bg-white"
                    >
                      <option value="cover">Cover</option>
                      <option value="contain">Contain</option>
                      <option value="auto">Auto</option>
                      <option value="100% 100%">Stretch</option>
                    </select>
                  </div>
                  
                  <div>
                    <label className="ev2-text-xs ev2-font-medium ev2-text-gray-600 ev2-mb-1 ev2-block">
                      Position
                    </label>
                    <select
                      value={backgroundPosition}
                      onChange={(e) => onBackgroundPositionChange(e.target.value)}
                      className="ev2-w-full ev2-px-2 ev2-py-1 ev2-text-sm ev2-border ev2-border-gray-300 ev2-rounded-md focus:ev2-ring-2 focus:ev2-ring-purple-500 focus:ev2-border-transparent ev2-bg-white"
                    >
                      <option value="center">Center</option>
                      <option value="top">Top</option>
                      <option value="bottom">Bottom</option>
                      <option value="left">Left</option>
                      <option value="right">Right</option>
                      <option value="top left">Top Left</option>
                      <option value="top right">Top Right</option>
                      <option value="bottom left">Bottom Left</option>
                      <option value="bottom right">Bottom Right</option>
                    </select>
                  </div>
                  
                  <div>
                    <label className="ev2-text-xs ev2-font-medium ev2-text-gray-600 ev2-mb-1 ev2-block">
                      Repeat
                    </label>
                    <select
                      value={backgroundRepeat}
                      onChange={(e) => onBackgroundRepeatChange(e.target.value)}
                      className="ev2-w-full ev2-px-2 ev2-py-1 ev2-text-sm ev2-border ev2-border-gray-300 ev2-rounded-md focus:ev2-ring-2 focus:ev2-ring-purple-500 focus:ev2-border-transparent ev2-bg-white"
                    >
                      <option value="no-repeat">No Repeat</option>
                      <option value="repeat">Repeat</option>
                      <option value="repeat-x">Repeat X</option>
                      <option value="repeat-y">Repeat Y</option>
                    </select>
                  </div>
                </div>
              </>
            )}
          </div>
        </div>
      )}

      {/* Units indicator */}
      <div className="ev2-text-center ev2-text-xs ev2-text-gray-500">
        All spacing values in {unit}
      </div>
    </div>
  );
}

export default SpacingCompass;