import React from 'react';
import { useDroppable } from '@dnd-kit/core';
import clsx from 'clsx';

function SectionElement({ element, renderElements }) {
  const [isHovered, setIsHovered] = React.useState(false);
  const { setNodeRef, isOver } = useDroppable({
    id: `${element.id}-droppable`,
    data: {
      parentId: element.id,
      position: element.children?.length || 0,
    },
  });

  const { 
    margin,
    border,
    borderStyle,
    borderColor,
    padding, 
    backgroundColor, 
    backgroundImage,
    backgroundSize,
    backgroundPosition,
    backgroundRepeat
  } = element.props;

  return (
    <div
      ref={setNodeRef}
      className={clsx(
        'ev2-min-h-[50px] ev2-transition-all',
        isOver && 'ev2-bg-primary-50'
      )}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      style={{
        marginTop: margin ? `${margin.top}px` : undefined,
        marginRight: margin ? `${margin.right}px` : undefined,
        marginBottom: margin ? `${margin.bottom}px` : undefined,
        marginLeft: margin ? `${margin.left}px` : undefined,
        borderTopWidth: border && borderStyle !== 'none' ? `${border.top}px` : undefined,
        borderRightWidth: border && borderStyle !== 'none' ? `${border.right}px` : undefined,
        borderBottomWidth: border && borderStyle !== 'none' ? `${border.bottom}px` : undefined,
        borderLeftWidth: border && borderStyle !== 'none' ? `${border.left}px` : undefined,
        borderStyle: borderStyle !== 'none' ? borderStyle : undefined,
        borderColor: borderStyle !== 'none' ? borderColor : undefined,
        paddingTop: `${padding.top}px`,
        paddingRight: `${padding.right}px`,
        paddingBottom: `${padding.bottom}px`,
        paddingLeft: `${padding.left}px`,
        backgroundColor: backgroundColor,
        backgroundImage: backgroundImage ? `url(${backgroundImage})` : undefined,
        backgroundSize: backgroundSize || 'cover',
        backgroundPosition: backgroundPosition || 'center',
        backgroundRepeat: backgroundRepeat || 'no-repeat',
      }}
    >
      {element.children && element.children.length > 0 ? (
        renderElements(element.children, element.id)
      ) : (
        <div className={clsx(
          "ev2-py-8 ev2-text-center ev2-text-sm ev2-border-2 ev2-border-dashed ev2-rounded ev2-transition-all",
          isHovered 
            ? "ev2-text-blue-500 ev2-border-blue-300" 
            : "ev2-text-gray-400 ev2-border-gray-300"
        )}>
          Drop elements here
        </div>
      )}
    </div>
  );
}

export default SectionElement;